.rounded-image {
    border-radius: 5%;
  }
  
.work-section-info {
    animation-duration: 5s;
    animation-iteration-count: infinite;
  }
  
  .breathing-effect {
    animation-name: breathing;
  }
  
  @keyframes breathing {
    0% {
      transform: scale(1);
    }
    20% {
      transform: scale(1.05);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .delay-1 {
    animation-delay: 0.2s;
  }
  
  .delay-2 {
    animation-delay: 0.2s;
  }
  
  .delay-3 {
    animation-delay: 0.2s;
  }
  